import React, { Fragment } from "react";
import { Input, Select, Badge, Card, Popup } from '@scuf/common';
import baseServices from '../../services/base.services';

export default class TextoArea extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            fileError: "",
            value: { tag: "", desc: "", ds: "" },
            vTag: "",
            vDesc: "",
            vDS: "",
            datasources: "",
            tagsCSV: []

        };
    }

    handleOnChangeTag = (dato) => {
        this.setState({ vTag: dato.val });
        this.setState({ value: { tag: dato.val, desc: 'model_feature', ds: this.state.vDS } });

        if (dato.val === "") {
            this.props.onRemoveDato(dato);
        }
        else {
            if (this.state.vDS && !this.state.vDS == "" && dato.val && !dato.val == "") {
                this.props.onDato({ key: dato.key, id: dato.id, val: { tag: dato.val, desc: 'model_feature', ds: this.state.vDS } });
            }
        }
    }

    handleOnChangeDS = (dato) => {
        //this.setState({ value: dato.val });

        this.setState({ vDS: dato.val });
        // this.setState({ value: { tag: this.state.vTag, desc: 'model_feature', ds: dato.val } });
        this.setState({ value: { tag: '', desc: 'model_feature', ds: dato.val } });
        this.getCSVTags(dato.val);


        this.props.onRemoveDato(dato);

        // if (dato.val === "" ) {
        //     this.props.onRemoveDato(dato);
        // }
        // else {
        //     if (this.state.vTag && !this.state.vTag == "" && dato.val && !dato.val == "") {
        //         //this.props.onDato({ key: dato.key, id: dato.id, val: { tag: this.state.vTag, desc: 'model_feature', ds: dato.val } });
        //         this.props.onDato({ key: dato.key, id: dato.id, val: { tag: '', desc: 'model_feature', ds: dato.val } });
        //     }
        // }

    }



    componentWillMount() {
        this.getData();
        var nvalue = ""
        var ntag = ""
        var ndesc = "model_feature"
        var nds = ""
        this.props.items.map((item) => {
            nvalue = item.value
            ntag = item.tag
            nds = item.ds
        }
        )

        //console.log(this.props.item)

        this.setState({ value: { tag: ntag, desc: ndesc, ds: nds } });
        //this.setState({ value: nvalue });
        this.setState({ vTag: nvalue.tag });
        this.setState({ vDesc: 'model_feature' });
        this.setState({ vDS: nvalue.ds });
        //console.log(this.props.items)
    }

    async componentDidMount() {

        // var nvalue = ""
        // this.props.items.map((item) =>
        //     nvalue = item.value
        // )

        //console.log(this.state.value.tag)

        if (this.state.value !== "") {
            //var dato = await { key: this.props.idGrupo, id: 1, val: this.props.items[0].ds }
            var dato = await { key: this.props.idGrupo, id: 1, val: this.state.vDS ? this.state.vDS : this.props.items[0].ds }
            this.handleOnChangeDS(dato)

            var dato2 = await { key: this.props.idGrupo, id: 1, val: this.state.vTag ? this.state.vTag : this.props.items[0].tag }
            this.handleOnChangeTag(dato2)
        }

    }

    getData = async () => {
        try {
            const url = 'datasources'
            const body = '?id=0'
            var response = await baseServices.getData(body, url);
            this.setState({ datasources: response })
        } catch (err) { }
        //console.log(this.state.datasources.data);
    }


    getCSVTags = async (csv) => {
        try {
            const url = 'http://10.63.2.160:3000/parseCSV/' + csv
            const body = ''
            var response = await baseServices.getCSVTags(body, url);
            const tagsINPUT = [];
            const tagRegex = /^[a-zA-Z0-9\s*_.]+$/;
            response.data.resp.forEach(function (tag, index) {
                //if (tagRegex.test(tag)) 
                if (!tag.includes('"') && !tag.includes("'") && !tag.includes('/') && !tag.includes('\\'))
                    tagsINPUT.push({ "value": tag, "text": tag })
            });
            if (tagsINPUT.length > 0)
                this.setState({ tagsCSV: tagsINPUT })
            else
                this.setState({ tagsCSV: [] })
        } catch (err) {
            this.setState({ tagsCSV: [] })
        }
    }

    render() {

        var idGrupo = this.props.idGrupo
        return (

            <React.Fragment>
                {/* {JSON.stringify(this.state.value)}<br /> */}
                {/* {JSON.stringify(this.state.datasources.data)}<br /> */}
                {this.props.items.map((item) => (
                    <React.Fragment>
                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                        <Badge color={(this.state.value === "" || this.state.value.tag === "" || this.state.value.desc === "" || this.state.value.ds === "" || !this.state.value.tag || !this.state.value.desc || !this.state.value.ds) ? "red" : "green"} empty={true} >&nbsp;</Badge>

                        <Popup className="popup-theme-wrap" element={item.label} position="right center" on="hover">
                            <Card>
                                <Card.Content>
                                    Tags mnimos (2) requeridos para entrenar el modelo
                                </Card.Content>
                            </Card>
                        </Popup>


                        <Select
                            key={item.id + 30}
                            placeholder={"Seleccione el Data Source"}
                            fluid={true}
                            //options={[{ value: 'mongo_results_dmh', text: 'mongo_results_dmh' }, { value: 'mongo_data_coord', text: 'mongo_data_coord' }, { value: 'mongo_data_pervsent', text: 'mongo_data_pervsent' }, { value: 'SGO3', text: 'SGO3' }]}
                            options={this.state.datasources.data ? this.state.datasources.data : [{}]}
                            value={this.state.value.ds}
                            onChange={(value) => {
                                this.handleOnChangeDS({ key: idGrupo, id: item.id, val: value });
                                // this.handleOnChangeTag({ key: idGrupo, id: item.id, val: "" })
                            }
                            }
                        />

                        {
                            this.state.tagsCSV.length > 0 ?
                                <Select
                                    key={item.id + 50}
                                    placeholder={"Seleccione el Tag"}
                                    fluid={true}
                                    options={this.state.tagsCSV ? this.state.tagsCSV : [{}]}
                                    value={this.state.value.tag}
                                    onChange={(value) => this.handleOnChangeTag({ key: idGrupo, id: item.id, val: value })}
                                />
                                :
                                <Input
                                    key={item.id + 10}
                                    placeholder={"Ingrese el Nombre del Tag"}
                                    fluid={true}
                                    rows={1}
                                    value={this.state.value.tag}
                                    onChange={(value) => this.handleOnChangeTag({ key: idGrupo, id: item.id, val: value })}

                                />
                        }





                    </React.Fragment>
                ))}
            </React.Fragment>
        );
    }
}
