import React from "react";
import { Input, Select, Card, Popup, Button, Icon } from '@scuf/common';
import baseServices from '../../services/base.services';

export default class TextoArea extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            fileError: "",
            value: { tag: "", desc: "", ds: "", of: "" },
            vTag: "",
            vDesc: "",
            vDS: "",
            vOF: "",
            datasources: "",
            tagsCSV: []
        };
    }


    handleOnChangeOF = (dato) => {

        var regexNUM = /[-+][0-9]+\.[0-9]+$/;
        var regexINT = /^[0-9]+$/;

        var datoVal = dato.val;
        var numVal = datoVal.slice(1, 100)

        if (datoVal.charAt(0) == ">" || datoVal.charAt(0) == "<" || datoVal.charAt(0) == "=") {
            datoVal = dato.val
            if (numVal.charAt(0) == "-" || numVal.slice(-1) == ".")
                datoVal = dato.val
            // else if (datoVal.length > 1 && !regexINT.test(numVal))
            //     datoVal = ""
        }
        else datoVal = ""

        if (datoVal.length > 2) {
            if (datoVal.slice(-1) == "-")
                datoVal = ""

            else if (datoVal.split(".").length > 2)
                datoVal = ""

            if (datoVal.slice(-1) == ".")
                datoVal = datoVal
            else if (!regexINT.test(datoVal.slice(-1)))
                datoVal = ""
        }

        if (datoVal.length == 2) {
            if (regexINT.test(datoVal.slice(-1)))
                datoVal = datoVal
            else if (datoVal.slice(-1) == "-")
                datoVal = datoVal
            else
                datoVal = ""
        }

        // if (regexNUM.test(datoVal.slice(1, 100))) {
        // }



        this.setState({ vOF: datoVal });
        this.setState({ value: { tag: this.state.vTag, desc: this.state.vDesc, ds: this.state.vDS, of: datoVal } });

        if (datoVal === "") {
            this.props.onRemoveDato(dato);
        }
        else {
            if (datoVal && !datoVal == "" && this.state.vDesc && !this.state.vDesc == "" && this.state.vDS && !this.state.vDS == "") {
                this.props.onDato({ key: dato.key, id: dato.id, val: { tag: this.state.vTag, desc: this.state.vDesc, ds: this.state.vDS, of: datoVal } });
            }
        }
    }

    handleOnChangeTag = (dato) => {
        this.setState({ vTag: dato.val });
        this.setState({ value: { tag: dato.val, desc: this.state.vDesc, ds: this.state.vDS, of: this.state.vOF } });

        if (dato.val === "" || (this.state.vDesc === "operational_filter" && this.state.vOF === "")) {
            this.props.onRemoveDato(dato);
        }
        else {
            if (dato.val && !dato.val == "" && this.state.vDesc && !this.state.vDesc == "" && this.state.vDS && !this.state.vDS == "") {
                this.state.vDesc == "operational_filter" ? this.props.onDato({ key: dato.key, id: dato.id, val: { tag: dato.val, desc: this.state.vDesc, ds: this.state.vDS, of: this.state.vOF } })
                    : this.props.onDato({ key: dato.key, id: dato.id, val: { tag: dato.val, desc: this.state.vDesc, ds: this.state.vDS } });
            }
        }
    }

    handleOnChangeDesc = (dato) => {
        //this.setState({ value: dato.val });
        this.setState({ vDesc: dato.val });
        this.setState({ value: { tag: this.state.vTag, desc: dato.val, ds: this.state.vDS } });

        if (dato.val === "operational_filter") {
            this.props.onRemoveDato(dato);
        }
        else {
            if (this.state.vTag && !this.state.vTag == "" && dato.val && !dato.val == "" && this.state.vDS && !this.state.vDS == "") {
                this.props.onDato({ key: dato.key, id: dato.id, val: { tag: this.state.vTag, desc: dato.val, ds: this.state.vDS } });
            }
        }
    }

    handleOnChangeDS = (dato) => {
        //this.setState({ value: dato.val });
        this.setState({ vDS: dato.val });
        //this.setState({ value: { tag: this.state.vTag, desc: this.state.vDesc, ds: dato.val } });
        this.setState({ value: { tag: '', desc: this.state.vDesc, ds: dato.val } });
        this.getCSVTags(dato.val);

        this.props.onRemoveDato(dato);

        // if (dato.val === "" || true) {
        //     this.props.onRemoveDato(dato);
        // }
        // else {
        //     if (this.state.vTag && !this.state.vTag == "" && this.state.vDesc && !this.state.vDesc == "" && dato.val && !dato.val == "") {
        //         this.props.onDato({ key: dato.key, id: dato.id, val: { tag: this.state.vTag, desc: this.state.vDesc, ds: dato.val } });
        //     }
        // }
    }



    componentWillMount() {
        this.getData();
        var nvalue = ""
        this.props.items.map((item) =>
            nvalue = item.value
        )
        this.setState({ value: nvalue });
        this.setState({ vTag: nvalue.tag });
        this.setState({ vDesc: nvalue.desc });
        this.setState({ vDS: nvalue.ds });
        this.setState({ vOF: nvalue.att });
        //console.log(this.props.items)
    }

    async componentDidMount() {

        // var nvalue = ""
        // this.props.items.map((item) =>
        //     nvalue = item.value
        // )

        //console.log(this.props.items)

        if (this.state.value !== "") {

            //var dato = await { key: this.props.idGrupo, id: 1, val: this.props.items[0].ds }
            var dato = await { key: this.props.idGrupo, id: 1, val: this.state.vDS ? this.state.vDS : this.props.items[0].ds }
            this.handleOnChangeDS(dato)

            //var dato2 = await { key: this.props.idGrupo, id: 1, val: this.props.items[0].tag }
            var dato2 = await { key: this.props.idGrupo, id: 1, val: this.state.vTag ? this.state.vTag : this.props.items[0].tag }
            this.handleOnChangeTag(dato2)

            //var dato3 = await { key: this.props.idGrupo, id: 1, val: this.props.items[0].desc }
            var dato3 = await { key: this.props.idGrupo, id: 1, val: this.state.vDesc ? this.state.vDesc : this.props.items[0].desc }
            this.handleOnChangeDesc(dato3)

            var dato4 = await { key: this.props.idGrupo, id: 1, val: this.state.vOF ? this.state.vOF : this.props.items[0].att }
            this.handleOnChangeOF(dato4)

        }

    }

    getData = async () => {
        try {
            const url = 'datasources'
            const body = '?id=0'
            var response = await baseServices.getData(body, url);
            this.setState({ datasources: response })
        } catch (err) { }
        //console.log(this.state.datasources.data);
    }

    getCSVTags = async (csv) => {
        try {
            const url = 'http://10.63.2.160:3000/parseCSV/' + csv
            const body = ''
            var response = await baseServices.getCSVTags(body, url);
            const tagsINPUT = [];
            response.data.resp.forEach(function (tag, index) {
                if (!tag.includes('"') && !tag.includes("'") && !tag.includes('/') && !tag.includes('\\'))
                    tagsINPUT.push({ "value": tag, "text": tag })
            });
            if (tagsINPUT.length > 0)
                this.setState({ tagsCSV: tagsINPUT })
            else
                this.setState({ tagsCSV: [] })
        } catch (err) {
            this.setState({ tagsCSV: [] })
        }
    }

    render() {

        var idGrupo = this.props.idGrupo
        return (

            <React.Fragment>

                {/* {JSON.stringify(this.state.value)}<br /> */}
                {this.props.items.map((item) => (
                    <React.Fragment>
                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                        {/* <Badge color={(this.state.value === "" || this.state.value.tag === "" || this.state.value.desc === "" || this.state.value.ds === "" || !this.state.value.tag || !this.state.value.desc || !this.state.value.ds) ? "red" : "green"} empty={true} >&nbsp;</Badge> */}
                        <Popup className="popup-theme-wrap" element={item.label} position="right center" on="hover">
                            <Card>
                                <Card.Content>
                                    Tags opcionales para entrenar el modelo
                                </Card.Content>
                            </Card>
                        </Popup>
                        <div align="right">
                            <Button
                                type="link"
                                content={<Icon name="delete" size="small" root="common" />}
                                onClick={() => {
                                    this.handleOnChangeDS({ key: idGrupo, id: item.id, val: '' });
                                    this.handleOnChangeTag({ key: idGrupo, id: item.id, val: '' });
                                    this.handleOnChangeDesc({ key: idGrupo, id: item.id, val: '' });
                                    this.setState({ value: { tag: "", desc: "", ds: "" } });
                                }
                                }
                            />
                        </div>
                        <Select
                            key={item.id + 20}
                            placeholder={"Seleccione la Descripcion del Tag"}
                            fluid={true}
                            options={[{ value: 'model_feature', text: 'model_feature' }, { value: 'model_validation', text: 'model_validation' }, { value: 'operational_filter', text: 'operational_filter' }]}
                            value={this.state.value.desc}
                            onChange={(value) => this.handleOnChangeDesc({ key: idGrupo, id: item.id, val: value })}

                        />

                        <Select
                            key={item.id + 30}
                            placeholder={"Seleccione el Data Source"}
                            fluid={true}
                            //options={[{ value: 'mongo_results_dmh', text: 'mongo_results_dmh' }, { value: 'mongo_data_coord', text: 'mongo_data_coord' }, { value: 'mongo_data_pervsent', text: 'mongo_data_pervsent' }, { value: 'SGO3', text: 'SGO3' }]}
                            options={this.state.datasources.data ? this.state.datasources.data : [{}]}
                            value={this.state.value.ds}
                            onChange={(value) => {
                                this.handleOnChangeDS({ key: idGrupo, id: item.id, val: value });
                                //this.handleOnChangeTag({ key: idGrupo, id: item.id, val: "" })
                            }
                            }

                        />

                        {
                            this.state.tagsCSV.length > 0 ?
                                <Select
                                    key={item.id + 50}
                                    placeholder={"Seleccione el Tag"}
                                    fluid={true}
                                    options={this.state.tagsCSV ? this.state.tagsCSV : [{}]}
                                    value={this.state.value.tag}
                                    onChange={(value) => this.handleOnChangeTag({ key: idGrupo, id: item.id, val: value })}
                                />
                                :
                                <Input
                                    key={item.id + 10}
                                    placeholder={"Ingrese el Nombre del Tag"}
                                    fluid={true}
                                    rows={1}
                                    value={this.state.value.tag}
                                    onChange={(value) => this.handleOnChangeTag({ key: idGrupo, id: item.id, val: value })}
                                />
                        }


                        {
                            this.state.value.desc === 'operational_filter' ?
                                <Popup className="popup-theme-wrap"
                                    position="right center"
                                    on="hover"
                                    element={
                                        <Input
                                            key={item.id + 90}
                                            placeholder={"Ingrese el Filtro Operacional"}
                                            fluid={true}
                                            rows={1}
                                            value={this.state.value.of}
                                            //type="number"
                                            onChange={(value) => this.handleOnChangeOF({ key: idGrupo, id: item.id, val: value })}
                                        />
                                    }
                                >
                                    <Card>
                                        <Card.Content>
                                            {"Condicin (>, <, =)"} <br />  {"seguido del valor."} <br />  {"Ejemplo: >20.75"}
                                        </Card.Content>
                                    </Card>
                                </Popup>


                                :
                                <div />

                        }


                    </React.Fragment>
                ))}

            </React.Fragment>
        );
    }
}
