import React, { Fragment } from "react";
import { Checkbox, Badge } from '@scuf/common';

export default class RadioGroup extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            value: ""
        };

        this.textInput = null;
        this.setTextInputRef = element => {
            this.textInput = element;
        };
        this.focusTextInput = () => {
            if (this.textInput) this.textInput.focus();
        };

    }

    handleOnChange = (dato) => {
        this.setState({ value: dato.val });
        this.props.onDato(dato);
        this.textInput.focus();
        //alert(JSON.stringify(dato))
        /*window.scrollTo(1, 100);*/
    }

    handleChecked(a, b) {
        return a === b
    }


    componentWillMount() {

        var nvalue = ""
        this.props.items.map((item) =>
            nvalue = item.value
        )
        this.setState({ value: nvalue });



    }

    async componentDidMount() {

        if (this.state.value !== "") {
            var dato = await { key: this.props.idGrupo, id: 1, val: this.state.value == "1" ? 1 : 0 }
            this.handleOnChange(dato)
        }

    }




    render() {
        var idGrupo = this.props.idGrupo
        return (
            <React.Fragment>

                <button className="oculto" ref={this.setTextInputRef} />
                {/* <Badge color={this.state.value === "" ? "red" : "green"} empty={true}>&nbsp;</Badge> */}
                {this.props.items.map((item) => (

                    <React.Fragment>
                        <Checkbox key={item.id}
                            label={item.label}
                            toggle={true}
                            disabled={false}
                            name="RadioGroup"
                            checked={this.state.value == "1" ? true : false}
                            onChange={(checked) => this.handleOnChange({ key: idGrupo, id: item.id, val: checked ? 1 : 0 })}
                        />
                    </React.Fragment>
                ))}

            </React.Fragment>
        );
    }
}
