import React, { useState, useEffect } from 'react'
import { Badge, Loader, Grid } from '@scuf/common'
import Highcharts from 'highcharts/highstock'
import HighchartsReact from 'highcharts-react-official'
import HChartDataAPI from './HChartDataAPI'
import './hchart.css'
//import './custom.css'
import baseServices from '../../../services/base.services';

require('highcharts/modules/exporting')(Highcharts)
require('highcharts/modules/export-data')(Highcharts)


const Midata = [
    { "name": "1", data: [[1641899579000, 0.55], [1641899639000, 5], [1641899699000, 0.55], [1641899759000, 0.55]] },
    { "name": "2", data: [[1641899579000, 1.55], [1641899639000, 1], [1641899699000, 1.55], [1641899759000, 1.55]] },
    { "name": "3", data: [[1641899579000, 1], [1641899639000, 2], [1641899699000, 3], [1641899759000, 4]] },
    { "name": "4", data: [[1641899579000, 0], [1641899639000, 0], [1641899699000, 0], [1641899759000, 2]] }
]



const HChart = ({ url, body, heigth, title, subtitle }) => {

    const [state, setState] = useState({ line: [], response: [], hidden: "nohidden" })


    useEffect(() => {

        const getData = async () => {

            try {
                if (body) {
                    var response = await baseServices.getData(body, url);
                    if (response.data && response.data.length > 0) {
                        //dispatch(startLineChart(response.data[0]))                    
                        setState({ line: HChartDataAPI(response), response: response.data, hidden: "hidden" })
                        setTimeout(() => {
                            getData()
                        }, 150000);

                    }
                } else {
                }

            } catch (err) {
                setTimeout(() => {
                    getData()
                }, 15000);

            }


        }

        getData()

    }, [body, state.hidden])

    const options = {

        chart: {
            type: 'line', //'area',
            height: heigth,
            zoomType: "xy"
        },

        boost: {
            useGPUTranslations: true,
            // Chart-level boost when there are more than 5 series in the chart
            seriesThreshold: 5
        },

        lang: {
            decimalPount: "."
        },
        title: {
            text: title
        },
        subtitle: {
            text: subtitle
        },
        tooltip: {
            backgroundColor: '#000000',
            style: {
                color: 'white'
            }
        },
        credits: {
            enabled: false
        },
        exporting: {
            enabled: true
        },
        legend: {
            enabled: true
        },
        yAxis: {
            gridLineWidth: 0.05
        },
        xAxis: {
            // plotBands: [{
            //     color: "#91e8e1", 
            //     from: 1639526400000,
            //     to: 1639569600000 
            // }]
            plotBands: state.line.Bandas
        },
        colors: ["#7cb5ec", "#90ed7d", "#f7a35c", "#8085e9", "#f15c80", "#e4d354", "#2b908f", "#f45b5b", "#91e8e1", "#fbe5ba"],
        //colors: [state.line.data[0].color],
        //series: state.line.data//[{ name: state.line.data[0].serie, data: state.line.data }]
        series: state.line.Lineas//Midata

    }

    return (
        <React.Fragment>
            {/* {JSON.stringify(state.line.Bandas)} <br /><br /> */}

            <div className={state.hidden}>
                <Loader text="Abriendo..." minHeight={400}>
                </Loader>
            </div>

            <div className='hcharts'>
                <HighchartsReact
                    highcharts={Highcharts}
                    constructorType={'stockChart'}
                    options={options}
                />
            </div>
            {
                //const colors = ['#8c8988','#4d6fab', '#dc824f', '#55a665', '#c34c50', '#816fae', '#93765d', '#65b1c7']
                state.response ?
                    <Grid.Row>
                        <Grid.Row>
                            <br />Cluster:<br />
                        </Grid.Row>
                        <Grid.Column width={6}>
                            {JSON.stringify(state.response).includes('"cluster":0') && <div class='box clust0'>0</div>}
                            {JSON.stringify(state.response).includes('"cluster":1') && <div class='box clust1'>1</div>}
                            {JSON.stringify(state.response).includes('"cluster":2') && <div class='box clust2'>2</div>}
                            {JSON.stringify(state.response).includes('"cluster":3') && <div class='box clust3'>3</div>}
                            {JSON.stringify(state.response).includes('"cluster":4') && <div class='box clust4'>4</div>}
                            {JSON.stringify(state.response).includes('"cluster":5') && <div class='box clust5'>5</div>}
                        </Grid.Column>
                    </Grid.Row>
                    : <React.Fragment />
            }
        </React.Fragment>

    )

}

export default HChart;