const HChartDataAPI = (response) => {

    const DatosJson = response.data
    const datosLine = []
    const datosBand = []
    const uniqueSeries = []

    //Obtendo los array con las series. Solo 1 registro por serie y por (uniqueSeries)
    const PrimerItem = DatosJson[0].data
    Object.keys(PrimerItem).map((key, i) => (
        uniqueSeries.push(key)
    ))
    //console.log(uniqueSeries)

    // recorro el array con las distintas series y luego recorro los datos del API para crear el array datosSeries
    uniqueSeries.map((f, j) => {
        const datosSerie = []
        const datosBanda = []
        var Banda_Actual
        var Banda_Siguiente
        var Banda_From = DatosJson[0].x

        for (let index = 0; index < DatosJson.length; index++) {
            const element = DatosJson[index];
            datosSerie.push([parseFloat(element.x), element.data[f]]);

            Banda_Actual = element.cluster
            Banda_Siguiente = index + 1 < DatosJson.length ? DatosJson[index + 1].cluster : DatosJson[index].cluster

            if (index == DatosJson.length - 1)
                Banda_Siguiente = "-2"


            if (Banda_Actual == Banda_Siguiente) {
                if (Banda_From == element.x)
                    Banda_From = parseFloat(element.x)
            }
            else {
                datosBanda.push({ from: Banda_From, to: parseFloat(element.x), color: element.color, DT: element.date })
                Banda_From = element.x
            }




        }


        datosLine.push(
            {
                name: uniqueSeries[j],
                data: datosSerie
            }
        )

        datosBand.push(datosBanda)


    }
    )

    return { Lineas: datosLine, Bandas: datosBand[0] }
}

export default HChartDataAPI




